import * as JWF from "javascript-window-framework";

function Sample001(): void {
  const win = new JWF.FrameWindow(); //
  win.setTitle("Sample01 "); //
  win.setPos(); //
}

function Sample002(): void {
  const win = new JWF.FrameWindow(); //
  win.setTitle("Sample02 "); //
  win.setSize(100, 100); //
  win.setPos(10, 10); //
  const client = win.getClient(); //
  win.addEventListener(
    "active",
    (e): void => {
      client.innerText = e.active
        ? ""
        : "";
    }
  );
}

function Sample003(): void {
  const listView = new JWF.ListView({ frame: true }); //
  listView.setTitle("Sample03 "); //
  listView.addHeader(["", "", ""]); //
  listView.addItem([1, "", 5]); //
  listView.addItem([2, "", 10]);
  listView.addItem([3, "", 10]);
  listView.setPos(30, 30); //

  listView.addEventListener(
    "itemClick",
    (e): void => {
      //
      const name = listView.getItemText(e.itemIndex, 1); //
      new JWF.MessageBox("", name + ""); //
    }
  );
}
function Sample004(): void {
  //
  let treeView = new JWF.TreeView({ frame: true });
  treeView.setTitle("Sample04 "); //
  //
  treeView.setSize(300, 300);
  //
  treeView.getRootItem().setItemText("");

  //
  let item: JWF.TreeItem;
  item = treeView.addItem("1");
  item.addItem("1-1");
  item.addItem("1-2");
  item = treeView.addItem("2");
  item.addItem("2-1");
  item.addItem("2-2");
  item.addItem;

  //
  treeView.addEventListener("itemSelect", function(e): void {
    const name = e.item.getItemText(); //
    new JWF.MessageBox("", name + ""); //
  });
}
function Sample005(): void {
  const frame = new JWF.FrameWindow();
  frame.setTitle("Sample005 "); //

  const splitter = new JWF.Splitter(); //
  frame.addChild(splitter, "client"); //

  const tree = new JWF.TreeView(); //
  const list = new JWF.ListView(); //

  splitter.addChild(0, tree, "client"); //splitter0tree
  splitter.addChild(1, list, "client"); //splitter1list

  //(WestEast)
  //we01
  //ns
  splitter.setSplitterPos(200, "we");
  //300
  splitter.setOverlay(true, 300);

  //tree
  tree.getRootItem().setItemText("");
  for (let j = 0; j < 5; j++) {
    let item = tree.addItem("" + j, true);
    for (let i = 0; i < 5; i++)
      item.addItem("" + j + "-" + i, false);
  }
  //
  tree.addEventListener(
    "itemSelect",
    (e): void => {
      const value = e.item.getItemText();
      if (value) {
        const no = list.getItemCount();
        const date = new Date().toLocaleString();
        list.addItem([no.toString(), value, date]);
      }
    }
  );

  //list
  list.addHeader(["", ["", 200], ""]);

  //
  frame.setSize(800, 600);
  frame.setPos();
}

//
addEventListener(
  "DOMContentLoaded",
  (): void => {
    Sample001();
    Sample002();
    Sample003();
    Sample004();
    Sample005();
  }
);
